import React from "react";
import Select from "../../../components/select";
import Autocomplete from "react-google-autocomplete";
import GoogleMapReact from "google-map-react";
import { Dropdown } from "primereact/dropdown";
class LocationAndCategory extends React.Component {
  state = {
    map: {
      lat: 59.95,
      lng: 30.33
    },
    error: false,
    page: 1,
    form: {
      category: null
    }
  };
  genderOptions = [
    {
      name: "Male",
      value: "M"
    },
    {
      name: "Female",
      value: "F"
    }
  ];
  changeGender = category => {
    const { form } = this.state;
    form.category = category.value;
    this.setState({ form });
    // this.props.onCategoryChange(category.value);
  };

  changeGender = gender => {
    const { form } = this.state;
    form.gender = gender.value;
    this.setState({ form });
  };

  onContinue = () => {
    this.props.moveNextPage();
  };

  placeSelectHandler = place => {
    console.log(place, "place");
  };

  render() {
    const { form, error } = this.state;
    const { onChange, categories, location, submitted } = this.props;
    console.log(categories, "categories from props");
    return (
      <div className="location-category">
        <h2 className="title">Location and category</h2>
        <p className="paragraph">
          Please select your project category and choose location of your
          proposed project
        </p>
        <div className="tab-label">
          <label>
            <span>Category</span>
            <Select
              useValue
              placeholder="Choose category"
              options={this.genderOptions}
              onChange={this.changeGender}
              value={form.category}
            />
          </label>
        </div>
        <div className="tab-label">
          <label>
            <span>Location</span>
            <input
              type="text"
              placeholder="Choose location"
              name="location"
              value={location}
              onChange={onChange}
              className={submitted && !location ? "G-invalid-input" : " "}
            />
          </label>
          <Autocomplete
            style={{ width: "100%" }}
            onPlaceSelected={this.placeSelectHandler}
            types={["establishment", "geocode"]}
          />
          <GoogleMapReact
            bootstrapURLKeys={{
              key: "AIzaSyBbpiFVdy5UIPua2fDDq4TPoBBAZx79feU"
            }}
            defaultCenter={this.props.center}
            defaultZoom={this.props.zoom}
          ></GoogleMapReact>
        </div>
        <div className="map"></div>
        <button className="G-button" onClick={this.onContinue}>
          Continue
        </button>
      </div>
    );
  }
}

export default LocationAndCategory;
